import * as React from 'react';

export default ({ piral }) => {

  const foo = piral.getData("foo");
  const changableFoo = piral.getData("changableFoo");

  const [storeData, setStoreData] = React.useState(null)
	React.useEffect(()=>{
		fetch('https://fakestoreapi.com/products')
            .then(res=>res.json())
            .then(json=>setStoreData([...json]))
	},[])

  return (
    <>
      <h1>Fetching Data from random API</h1>

      {storeData&&storeData.map(val=>{
        return <div style={{background:"yellow", margin: "10px"}}>
          <h3>{val.title}</h3>
          <img src={val.image} style={{width:"200px", height: "200px"}}></img>
        </div>
      })}
      {/* <p>getting data set while registering parent instance --- foo: {foo}</p>
      <p>getting data set while registering parent instance --- changableFoo: {changableFoo}</p> */}

    </>
  );
};
